          // Âä®ÊÄÅÂä†ËΩΩÂ≠êËäÇÁÇπ, Ê®°ÊãüajaxËØ∑Ê±ÇÊï∞ÊçÆ
         // ËØ∑Ê≥®ÊÑè id ‰∏çËÉΩÈáçÂ§çÂì¶„ÄÇ
         if(m.hasOwnProperty("children")){
            
            m.loadNode = 1; // Ê≠£Âú®Âä†ËΩΩËäÇÁÇπ

            setTimeout(()=>{

              m.loadNode = 2; // ËäÇÁÇπÂä†ËΩΩÂÆåÊØï

              m.isFolder = !m.isFolder; 

              m.children.push({
                  id:+new Date(),
                  parentId:m.id,
                  name:"Âä®ÊÄÅÂä†ËΩΩËäÇÁÇπ1",
                  path:"",
                  clickNode:false,
